export default [{
    path: '/forumPage',
    component: resolve => require(['@/views/forum/forumPage.vue'], resolve),
    children: [{
        path: '/forumPage/forumDetailsPage/:discussId',
        component: resolve => require(['@/views/forum/forumDetailsPage.vue'], resolve),
    }]
}]


