export default [{
    path: '/personalHomePage',
    component: resolve => require(['@/views/personalHomePage/personalHomePage.vue'], resolve),
    children: [{
        path: '/personalHomePage/forumPage',
        component: resolve => require(['@/views/personalHomePage/myForum.vue'], resolve),
        children: [{
            path: '/personalHomePage/forumPage/forumDetailsPage/:discussId',
            component: resolve => require(['@/views/forum/forumDetailsPage.vue'], resolve),
        }] 
    },{
        path: '/personalHomePage/msg',
        component: resolve => require(['@/views/personalHomePage/message.vue'], resolve),
    },{
        path: '/personalHomePage/collectionPage',
        component: resolve => require(['@/views/personalHomePage/collectionPage.vue'], resolve),
    },{
        path: '/personalHomePage/myClub',
        component: resolve => require(['@/views/personalHomePage/myClub.vue'], resolve),
    },{
        path: '/personalHomePage/publishInfo',
        component: resolve => require(['@/views/personalHomePage/publishInfo.vue'], resolve),
    },{
        path: '/personalHomePage/draft',
        component: resolve => require(['@/views/personalHomePage/draft.vue'], resolve),
    }]
    
}]


