export default [
  {
    path: '/write',
    component: resolve => require(['@/views/write/index.vue'], resolve),
    children: [
      {
        path: 'cover',
        component: resolve => require(['@/views/write/cover.vue'], resolve)
      },
      {
        path: 'club',
        component: resolve => require(['@/views/write/club.vue'], resolve)
      },
      {
        path: 'done',
        component: resolve => require(['@/views/write/done.vue'], resolve)
      },
      {
        path: 'edit',
        component: resolve => require(['@/views/write/edit.vue'], resolve)
      },
      {
        path: 'preview',
        component: resolve => require(['@/views/write/preview.vue'], resolve)
      }
    ]
  }
]



