import Axios from 'axios'
import { actionType } from '@/assets/js/actionType'
import port from '../../assets/js/port'

const state = {
    auditUserType: '',
    headimg: '',
    nickname: '',
    userId: '',
    userType: ''
}
const getters = {}
const mutations = {
    setUserInfo(state, _obj) {
        Object.assign(state, _obj);
    }
}
const actions = {
    [actionType.getUserInfo]({ commit, state }) {
        Axios.get(port.MY_PROT, {}).then((response) => {
                const { data } = response;
                if (response.code == 200) {
                    commit('setUserInfo', data);
                }
            })
            .catch((error) => {
                console.log(error.response.data);
            });
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}



