export default [{
        path: '/articleDetails/:contId',
        component: resolve => require(['@/views/articleDetails/articleDetailsPage.vue'], resolve),
        children: [{
            path: '/articleDetails/commentPage/:contId/:atUserId',
            component: resolve => require(['@/views/articleDetails/commentPage.vue'], resolve),
        }]
    },
    {
        path: '/productArticleDetailsPage/:contId',
        component: resolve => require(['@/views/articleDetails/productArticleDetailsPage.vue'], resolve),
        children: [{
            path: '/productArticleDetailsPage/commentPage/:contId/:atUserId',
            component: resolve => require(['@/views/articleDetails/commentPage.vue'], resolve),
        }]
    }
]


