import Axios from 'axios'
import {actionType} from '@/assets/js/actionType'

const state = {
  article: {
    productId: 0,
    contType: 1,
    cover: '',
    clubId: -1,
    title: '',
    desc: '',
    content: ''
  },
  post: {
    content: '',
    preimg: [],
    tagId: 0,
    clubId: 0,
  }
}
const getters = {}
const mutations = {
  [actionType.set_article_data](state, article = {productId, cover, clubId, title, desc, content}) {
    Object.assign(state.article, article)
  },
  [actionType.set_post_data](state, post = {content, preimg, tagId, clubId}) {
    Object.assign(state.post, post)
  }
}
const actions = {}

export default {
  state,
  getters,
  actions,
  mutations
}



