// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'
import store from '../src/store'
import {
  Swipe,
  SwipeItem,
  Cell,
  Button,
  Actionsheet,
  InfiniteScroll,
  Popup,
  Field,
  Checklist,
  DatetimePicker,
  Header,
} from 'mint-ui'
import '../src/assets/reset.css'
import 'mint-ui/lib/style.css'
import '../src/assets/iconfont/iconfont.css'
import '../src/assets/global.scss'
import { actionType } from '@/assets/js/actionType'
import port from '@/assets/js/port'

Vue.use(InfiniteScroll)
Vue.component(Swipe.name, Swipe)
Vue.component(SwipeItem.name, SwipeItem)
Vue.component(Cell.name, Cell)
Vue.component(Button.name, Button)
Vue.component(Actionsheet.name, Actionsheet)
Vue.component(Field.name, Field)
Vue.component(Checklist.name, Checklist)
Vue.component(Popup.name, Popup)
Vue.component(DatetimePicker.name, DatetimePicker)
Vue.component(Header.name, Header)
Vue.config.productionTip = false
Vue.prototype.$http = axios
Vue.prototype.$qs = qs
Vue.prototype.$actionType = actionType
Vue.prototype.$port = port

let query = router.match(location.href).query

// 获取微信配置
let getWXJSAPI = () => {
  // 清除token\code
  store.commit(actionType.set_token, '')
  store.commit(actionType.set_wx_code, '')
  new Promise((resolve, reject) => {
    axios.get(port.GET_WX_CONFIG, {
      params: {
        url: decodeURIComponent(location.href)
      }
    }).then(res => {
      if (res.code === 200) {
        location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${res.data.appid}&redirect_uri=${encodeURIComponent(location.href)}&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`
        resolve(res)
      } else {
        reject(res)
      }
    })
  })
}
if (Boolean(query.code)) {
  // 来源微信授权
  // 储存code
  query.code && store.commit(actionType.set_wx_code, query.code instanceof Array ? query.code[0] : query.code)
  // 过滤参数
  Object.getOwnPropertyNames(query).forEach(e => {
    if (e === 'code' ||
      e === 'from' ||
      e === 'state') {
      delete query[e]
    } else if (query[e] instanceof Array) {
      query[e] = query[0]
    }
  })
  // 跳转
  location.href = `${location.origin}/hornCultureProject/dist/index.html${Object.getOwnPropertyNames(query).length ? `?${qs.stringify(query)}` : ''}`
} else {
  // axios返回拦截
  axios.defaults.crossDomain = true
  axios.defaults.withCredentials = true
  axios.interceptors.request.use(config => {
    config.params = {...config.params, token: store.state.token}
    return config
  }, err => {
    console.log(err)
    return Promise.reject(err)
  })
  axios.interceptors.response.use(res => {
    if (res.data.code === 403) {
      // token过期
      getWXJSAPI()
    }
    return res.data
  }, err => {
    console.log(err)
    return Promise.reject(err)
  })
  router.beforeEach((from, to, next) => {
    next()
  })
  new Promise((resolve, reject) => {
    if (!store.state.token) {
      if (store.state.code) {
        axios.get(port.GET_TOKEN, {
          params: {
            code: store.state.code,
            state: 'state',
          }
        }).then(res => {
          if (res.code === 200) {
            // 获取token成功
            store.commit(actionType.set_token, res.data)
            console.log(res.data)
            resolve(res)
          } else {
            reject()
            // resolve()
          }
        })
      } else {
        reject()
        // resolve()
      }
    } else {
      resolve({})
    }
  }).then(res => {
    // 获取系统配置
    axios.get(port.GET_CONFIG, {
      params: {
        token: store.state.token
      }
    }).then(({code, data, msg}) => {
      if (code === 200) {
        store.commit(actionType.set_config_data, {data})
      }
    })
    /* eslint-disable no-new */
    new Vue({
      el: '#app',
      router,
      store,
      axios,
      components: {App},
      template: '<App/>'
    })
  }).catch(res => {
    // 获取token失败
    getWXJSAPI()
  })
}



