import Axios from 'axios'
import { actionType } from '@/assets/js/actionType'

const state = {
  // AuditStatus: [],
  // ClubType: [],
  // CommentStatus: [],
  // CommentType: [],
  // ContStatus: [],
  // ContType: [],
  // ImgType: [],
  // JoinStatus: [],
  // Like: [],
  // MsgType: [],
  // TagStatus: [],
  // TagType: [],
}
const getters = {}
const mutations = {
  [actionType.set_config_data] (state, {data}) {
    Object.getOwnPropertyNames(data).forEach(e => {
      state[e] = []
      if (data[e] instanceof Array) {
        data[e].forEach(e2 => {
          let arr = e2.split(' - ')
          state[e].push({
            value: parseInt(arr[0]),
            label: arr[1]
          })
        })
      }
    })
  }
}
const actions = {}

export default {
  state,
  getters,
  actions,
  mutations
}



