export default [
  {
    path: '/detail',
    component: resolve => require(['@/views/detail/index.vue'], resolve),
    children: [
      {
        path: 'club',
        component: resolve => require(['@/views/detail/club.vue'], resolve),
      },
      {
        path: 'coach',
        component: resolve => require(['@/views/detail/coach.vue'], resolve)
      },
      {
        path: 'expert',
        component: resolve => require(['@/views/detail/expert.vue'], resolve)
      },
    ]
  },
  {
    path: '/auth/club',
    component: resolve => require(['@/views/auth/club.vue'], resolve)
  },
  {
    path: '/auth/coach',
    component: resolve => require(['@/views/auth/coach.vue'], resolve)
  },
  {
    path: '/auth/expert',
    component: resolve => require(['@/views/auth/expert.vue'], resolve)
  }
]



