export default [
  {
    path: '/post',
    component: resolve => require(['@/views/post/index.vue'], resolve),
    children: [
      {
        path: 'part',
        component: resolve => require(['@/views/post/part.vue'], resolve)
      },
      {
        path: 'edit',
        component: resolve => require(['@/views/post/edit.vue'], resolve)
      },
      {
        path: 'club',
        component: resolve => require(['@/views/post/club.vue'], resolve)
      }
    ]
  }
]



